function sliderWorkshop() {
    var swiper = new Swiper('.workshop .swiper', {
        slidesPerView: 3,
        spaceBetween: 47,
        navigation: {
            nextEl: ".workshop .swiper-button-next",
            prevEl: ".workshop .swiper-button-prev",
        },
        breakpoints: {
            320: {
                slidesPerView: 1,
                spaceBetween: 20,
            },
            451: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            767: {
                slidesPerView: 2,
                spaceBetween: 20,
            },
            993: {
                slidesPerView: 3,
                spaceBetween: 47,
            },
        }
    })
}

let SlidersArray = []

function sliderSeason() {
    $(".season__slider").each(function(index, el) {
        $(el).attr("data-slider-id", `${index}`)
        SlidersArray[index] = new Swiper(`.season__slider[data-slider-id="${index}"] .swiper`, {
            slidesPerView: "auto",
            spaceBetween: 0,
            navigation: {
                nextEl: `.season__slider[data-slider-id="${index}"] .swiper-button-next`,
                prevEl: `.season__slider[data-slider-id="${index}"] .swiper-button-prev`,
            },
        })
    })
}

$(document).ready(function() {

    sliderWorkshop();
    sliderSeason();

    // Select
    $('.select__list-default').click(function () {
        $(this).parent('.select__wrap').toggleClass('select__wrap--active')
    })

    $('.select__list li').click(function () {
        var currentele = $(this).html()
        $('.select__list-default li').html(currentele)
        $(this).parents('.select__wrap').removeClass('select__wrap--active')
        $(this).siblings(".select__list li").removeClass("li--active")
        $(this).addClass("li--active")
    })

    let selectLi = $('.select__list li')
    let selectWrap = $('.select__wrap')

    $(document).mouseup(function (e) {
        if ( !selectLi.is(e.target) && selectLi.has(e.target).length === 0 &&
            !selectWrap.is(e.target) && selectWrap.has(e.target).length === 0

        ) {
            selectWrap.removeClass('select__wrap--active')
        }
    })

    // Tabs
    $(".season__tab").click(function () {
        let path = $(this).attr("data-tab-path")
        $(this).siblings(".season__tab").removeClass("season__tab--active")
        $(this).addClass("season__tab--active")

        $(this).parents(".season__inner").find(".season__slider").removeClass("season__slider--active")
        $(this).parents(".season__inner").find(`.season__slider[data-content-path="${path}"]`).addClass("season__slider--active")
    })

})